<p>Having grown up in poverty, as a pastor’s son, Jim understood poverty by heart and wished to help people when he grew up. He was passionate about people in need from his childhood, and as a boy he enjoyed good times with an Assemblies of God missionary and his wife, one Ron and Gloria Handson from the Evangel University, USA who brought them toffees. He marveled at these stranger’s good hearts and wondered how they could possibly come to Africa to do the good they did. He wished to bless people like Ron and Gloria did when he grew up and in 2011, Rise Up Society (RUS) was birthed, a charitable organization geared towards helping the poorest of the poor in rural Africa.</p>

