import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/pages/Home'
import Contact from '@/components/pages/Contact'
import Donate from '@/components/pages/Donate'
import Founder from '@/components/pages/Founder'
import Jiggers from '@/components/pages/Jiggers'
import Voluteers from '@/components/pages/Voluteers'

Vue.use(Router)
export default new Router({
    mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
        path: '/contact_us',
        name: 'Contact',
        component: Contact
      },
      {
        path: '/donate',
        name: 'Donate',
        component: Donate
      },
      {
        path: '/jiggers',
        name: 'Jiggers',
        component: Jiggers
      }
      ,
      {
        path: '/founder',
        name: 'Founder',
        component: Founder
      }
      ,
      {
        path: '/volunteers',
        name: 'Voluteers',
        component: Voluteers
      }
  ]
})